'use server';
/**
 * @fileOverview Provides general information and care tips for a plant based on an image and/or description.
 *
 * - getPlantInfo - A function that handles fetching plant information.
 * - GetPlantInfoInput - The input type for the getPlantInfo function.
 * - GetPlantInfoOutput - The return type for the getPlantInfo function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const GetPlantInfoInputSchema = z.object({
  photoDataUri: z
    .string()
    .describe(
      "A photo of a plant, as a data URI that must include a MIME type and use Base64 encoding. Expected format: 'data:<mimetype>;base64,<encoded_data>'."
    ),
  description: z.string().optional().describe('An optional user-provided description of the plant or specific questions.'),
});
export type GetPlantInfoInput = z.infer<typeof GetPlantInfoInputSchema>;

const GetPlantInfoOutputSchema = z.object({
  isPlant: z.boolean().describe('Whether the image is identified as a plant.'),
  commonName: z.string().optional().describe('The common name of the plant, if identified.'),
  latinName: z.string().optional().describe('The Latin name of the plant, if identified.'),
  plantDescription: z.string().optional().describe('A general description of the plant, its appearance, and origin.'),
  careTips: z.object({
    sunlight: z.string().optional().describe('Sunlight requirements (e.g., "full sun", "partial shade", "indirect light").'),
    water: z.string().optional().describe('Watering guidelines (e.g., "water when top inch of soil is dry", "keep soil consistently moist").'),
    soil: z.string().optional().describe('Recommended soil type (e.g., "well-draining potting mix", "sandy soil").'),
    fertilizer: z.string().optional().describe('Fertilizing advice (e.g., "fertilize monthly during growing season", "use balanced liquid fertilizer").'),
    humidity: z.string().optional().describe('Humidity preferences (e.g., "prefers high humidity, mist regularly", "average room humidity is fine").'),
    pruning: z.string().optional().describe('Pruning tips (e.g., "prune in spring to encourage bushier growth", "remove dead or yellowing leaves").'),
  }).optional().describe('Comprehensive care tips for the plant.'),
  funFacts: z.array(z.string()).optional().describe('Up to 3 interesting or fun facts about the plant.'),
  error: z.string().optional().describe('Error message if plant cannot be identified or information not found. Provide this if isPlant is false or if no meaningful information can be retrieved.')
});
export type GetPlantInfoOutput = z.infer<typeof GetPlantInfoOutputSchema>;


export async function getPlantInfo(input: GetPlantInfoInput): Promise<GetPlantInfoOutput> {
  return getPlantInfoFlow(input);
}

const prompt = ai.definePrompt({
  name: 'getPlantInfoPrompt',
  input: {schema: GetPlantInfoInputSchema},
  output: {schema: GetPlantInfoOutputSchema},
  prompt: `You are a knowledgeable botanist and plant care expert.
  Based on the provided image and optional description, identify the plant and provide comprehensive information about it.

  Image: {{media url=photoDataUri}}
  {{#if description}}User Description/Questions: {{{description}}}{{/if}}

  Your tasks:
  1. Determine if the image contains a plant. Set 'isPlant' accordingly.
  2. If it is a plant, identify its common and Latin names. If unsure, state that.
  3. Provide a general description of the plant (appearance, origin, typical size).
  4. Offer detailed care tips covering: sunlight, watering, soil, fertilizing, humidity, and pruning. Make these practical and easy to understand for a home gardener.
  5. Share 2-3 interesting or fun facts about the plant.
  6. If the image is not a plant, or if you cannot confidently identify the plant or provide information, set 'isPlant' to false (if applicable) and provide a helpful message in the 'error' field. For example, "Could not identify the plant from the image provided. Please try a clearer image or add a description." or "The image does not appear to be a plant."

  Structure your output strictly according to the GetPlantInfoOutputSchema.
  If a field is optional and no information is found, omit it or provide a null/empty value as appropriate for the schema.
  Example for careTips if a specific tip is unknown: "soil": "General purpose potting mix."
  If no care tips can be provided at all, the careTips object itself can be omitted if your schema allows, or contain empty/null strings for its fields.
  Ensure funFacts is an array of strings.
  `,
});

const getPlantInfoFlow = ai.defineFlow(
  {
    name: 'getPlantInfoFlow',
    inputSchema: GetPlantInfoInputSchema,
    outputSchema: GetPlantInfoOutputSchema,
  },
  async (input) => {
    const {output} = await prompt(input);
    if (!output) {
        return {
            isPlant: false,
            error: "Failed to get a response from the AI model. Please try again."
        }
    }
    // Ensure funFacts is an array, even if LLM returns a single string or is missing
    if (output.funFacts && !Array.isArray(output.funFacts)) {
        // @ts-ignore
        output.funFacts = [String(output.funFacts)];
    } else if (!output.funFacts) {
        output.funFacts = [];
    }
    return output;
  }
);
