
'use server';
/**
 * @fileOverview Analyzes a plant disease from an image and suggests solutions.
 *
 * - analyzePlantDisease - A function that handles the plant disease analysis process.
 * - AnalyzePlantDiseaseInput - The input type for the analyzePlantDisease function.
 * - AnalyzePlantDiseaseOutput - The return type for the analyzePlantDisease function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const AnalyzePlantDiseaseInputSchema = z.object({
  photoDataUri: z
    .string()
    .describe(
      "A photo of a plant, as a data URI that must include a MIME type and use Base64 encoding. Expected format: 'data:<mimetype>;base64,<encoded_data>'."
    ),
  description: z.string().describe('The description of the plant.'),
});
export type AnalyzePlantDiseaseInput = z.infer<typeof AnalyzePlantDiseaseInputSchema>;

const AnalyzePlantDiseaseOutputSchema = z.object({
  diseaseDetected: z.boolean().describe('Whether a disease was detected or not.'),
  diseaseName: z.string().describe('The name of the detected disease, if any. If no disease, state "Healthy" or similar positive confirmation.'),
  suggestedSolutions: z
    .array(z.string())
    .describe('A list of suggested solutions for the detected disease. Prioritize common, accessible, and home-based remedies where appropriate. If healthy, provide general care tips or positive reinforcement.'),
  confidenceScore: z.number().optional().describe('A numerical score (0-1) indicating the confidence of the disease detection. Only include if a disease is detected.'),
  possibleCauses: z.array(z.string()).optional().describe('A list of potential causes for the detected disease or symptoms.'),
});
export type AnalyzePlantDiseaseOutput = z.infer<typeof AnalyzePlantDiseaseOutputSchema>;

export async function analyzePlantDisease(input: AnalyzePlantDiseaseInput): Promise<AnalyzePlantDiseaseOutput> {
  return analyzePlantDiseaseFlow(input);
}

const prompt = ai.definePrompt({
  name: 'analyzePlantDiseasePrompt',
  input: {schema: AnalyzePlantDiseaseInputSchema},
  output: {schema: AnalyzePlantDiseaseOutputSchema},
  prompt: `You are an expert botanist AI specializing in diagnosing plant diseases and recommending treatments, with a focus on solutions readily available to home gardeners.

  Analyze the provided plant image and description.

  Image: {{media url=photoDataUri}}
  Description: {{{description}}}

  Your analysis should include:
  1.  **Disease Detection**: Determine if a plant disease is present. Set 'diseaseDetected' to true or false.
  2.  **Disease Name**: If a disease is detected, identify its common name. If the plant appears healthy, state "Healthy" or a similar positive assessment.
  3.  **Suggested Solutions**:
      *   If a disease is detected, provide a list of practical, actionable solutions. Prioritize solutions that can be implemented at home (e.g., adjusting watering, pruning, using common household items if applicable like soapy water for aphids, or readily available organic treatments).
      *   If the plant is healthy, provide 2-3 general care tips relevant to what you can infer about the plant type, or simply a positive note like "Keep up the good care!".
  4.  **Confidence Score** (Optional but Recommended for Diseases): If a disease is detected, provide a confidence score between 0.0 and 1.0 for your diagnosis.
  5.  **Possible Causes** (Optional but Recommended for Diseases): If a disease is detected, list a few likely causes (e.g., overwatering, poor air circulation, specific pests).

  Structure your output strictly according to the AnalyzePlantDiseaseOutputSchema. Ensure all fields are appropriately filled based on your analysis.
  If no disease is detected, 'diseaseName' should reflect that (e.g., "Healthy"), and 'suggestedSolutions' can be general tips. 'confidenceScore' and 'possibleCauses' can be omitted if no disease is detected.
  `,
});

const analyzePlantDiseaseFlow = ai.defineFlow(
  {
    name: 'analyzePlantDiseaseFlow',
    inputSchema: AnalyzePlantDiseaseInputSchema,
    outputSchema: AnalyzePlantDiseaseOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    if (!output) {
        return {
            diseaseDetected: false,
            diseaseName: "Analysis Inconclusive",
            suggestedSolutions: ["Could not analyze the plant at this time. Please try again with a clearer image or description."],
        };
    }
    // Ensure suggestedSolutions is always an array
    if (output.suggestedSolutions && !Array.isArray(output.suggestedSolutions)) {
        // @ts-ignore
        output.suggestedSolutions = [String(output.suggestedSolutions)];
    } else if (!output.suggestedSolutions) {
        output.suggestedSolutions = [];
    }
    // Ensure possibleCauses is always an array if present
    if (output.possibleCauses && !Array.isArray(output.possibleCauses)) {
        // @ts-ignore
        output.possibleCauses = [String(output.possibleCauses)];
    } else if (output.possibleCauses === undefined) {
        output.possibleCauses = [];
    }

    return output;
  }
);
